.button {
  font-size: 18px;
  font-weight: 700;
  line-height: 20px;
  text-transform: uppercase;
  background-color: transparent;
  border: 2px solid var(--charcoal);
  color: var(--charcoal);
  text-decoration: none;
  text-align: center;

  @media (min-width: 768px) {
    font-size: 17px;
  }
}
